const translate_month = ( month ) => {
  var result = month;
  switch(month) {
    case 'Feb':
      result = 'Fev' ;
      break;
    case 'Apr':
      result = 'Abr' ;
      break;
    case 'May':
      result = 'Mai' ;
      break;
    case 'Aug':
      result = 'Ago' ;
      break;
    case 'Sep':
      result = 'Set' ;
      break;
    case 'Dec':
      result = 'Dez' ;
      break;
  }
  return result;
}

const translate_this_label = ( label ) => {
  let month = label.match(/Jan|Feb|Mar|Apr|May|Jun|Jul|Aug|Sep|Nov|Dec/g);
  if ( ! month ) 
    return label;
  let translation = translate_month( month[0] );
  return label.replace( month, translation, 'g' );
}

export { translate_this_label, translate_month }