<script>
// import HelloWorld from './components/HelloWorld.vue'
import { Chart } from 'chart.js'
import { jStat } from 'jstat'
import { translate_this_label } from './translate_month.js'
import { annotation } from 'chartjs-plugin-annotation'

Chart.plugins.register({
  annotation: annotation
});

const seedrandom = require('seedrandom')
window.jStat = jStat

import drs from './data.json';



const app = {
  name: 'App',
  components: {},
  data () {
    return {
      input: {
        qtdPassageiros: 1,
        largura: 3,
        comprimento: 20,
        distanciaSegura: 0.75,
        distanciamento: 40,
        adesaoMascara: 90,
        adesaoAstronauta: 0,
        casosPorHabitante: 50,
        curvaDeCarga: [0.1, 0.1, 0.2, 0.3, 0.5, 0.8, 0.9, 1, 1, 0.8, 0.7, 0.5, 0.5, 0.7, 0.9, 0.8, 0.6, 0.5, 0.4, 0.3, 0.3, 0.2, 0.1, 0.1]
      },
      tipo: undefined,
      drSel: undefined,
      dadosDrSelIndex: 0,
      cargaHorario: {
        horario: (new Date()).getHours(),
        aplicarCarga: true
      },
      emojis: [],
      seed: 123,
      veiculoChart: undefined,
      cargaChart: undefined,
      // fator que converte pixels para metros
      fatorEscala: undefined, 
      // espaço entre passageito e as paredes
      ppadd: 0.5, 
       // media da distribuição normal que descreve o tamanho do passageiro
      pRaioMean: 0.3,
      // desvio da distribuição normal que descreve o tamanho passageiro 
      pRaioStd: 0.05,
      // riscos
      riscos: {
        comCom: 0.2,
        comSem: 0.4,
        semCom: 0.3,
        semSem: 0.7
      },
      verTabelaCompleta: false,
      drs: drs
    }
  },
  watch: {

    dadosDrSelIndex () {
      this.drChange()
      if (this.casosChart) {
        this.casosChart.options.animation.duration = 0
        this.casosChart.annotation.elements['a-line-1'].options.value = this.casosDateLabels[parseInt(this.dadosDrSelIndex)]
        this.casosChart.update()
        this.casosChart.options.animation.duration = 400
      }


      let curva = this.input.casosPorHabitante ? 
        [0.5,0.2,0.2,0.1,0.8,2.1,6.2,8.1,6.1,4.6,4.1,4.0,9.5,7.8,5.5,4.4,5.1,9.2,8.3,5.0,3.0,2.2,2.0,1.0]
      : [0.3,0.1,0.1,0.0,0.4,1.3,11.1,9.0,3.9,2.7,2.7,5.4,15.3,7.3,3.6,3.0,5.1,9.6,9.9,3.3,1.7,1.3,2.0,0.9]
      
      this.input.curvaDeCarga = curva

      this.drawCurvaCarga()


    },
    drSel () {
      this.drawCasosDr()
      this.drChange()
    },
    verTabelaCompleta () {
      this.fixEmoji()
    },
    input: {
      handler: function () {
        this.drawVeiculo()
        this.fixEmoji()
      },
      deep: true
    },
    cargaHorario: {
      handler: function () {
        this.drawCurvaCarga()
        this.drawVeiculo()
      },
      deep: true
    }
  },
  methods: {
    fixEmoji () {
      // this.$nextTick(() => {
      //   window.setTimeout(() => {
      //     document.querySelectorAll('.emoji').forEach(e => e.remove())
      //   window.twemoji.parse(document.body)
      //   }, 50)
      // })

    },
    calculaPassageiros () {
      // atualiza passageiros com base na curva
      // definimos que no pico da curva o vagão está lotado.
      // TODO: poreriamos alterar para casos antes/pos pandemia
      let curvaMax = this.input.curvaDeCarga.reduce((a, c) => c > a ? c : a, 0)
      let cargaSel = this.input.curvaDeCarga[this.cargaHorario.horario]

      let qtdPassageiros = Math.floor(Math.round((this.input.lotacao/curvaMax) * cargaSel))

      this.input.qtdPassageiros = Math.max(1, qtdPassageiros)
    },
    defineOnibus () {
      this.input.largura = 2.5
      this.input.comprimento = 12
      this.input.lotacao = 30
      this.tipo = 'Onibus'
      this.calculaPassageiros()
    },
    defineTrem () {
      this.input.largura = 2.9
      this.input.comprimento = 17
      this.input.lotacao = 50
      this.tipo = 'Trem'
      this.calculaPassageiros()
    },
    defineMetro () {
      this.input.largura = 2.9
      this.input.comprimento = 18
      this.input.lotacao = 50
      this.tipo = 'Metro'
      this.calculaPassageiros()
    },
    pClass (item)  {
      let obj = {}
      if (item < 0.3) {obj['has-text-success-dark'] = true ; return obj;}
      if (item < 0.4) {obj['has-text-warning-dark'] = true ; return obj;}
      if (item < 0.6) {obj['has-text-warning'] = true ; return obj;}  
      
      obj['has-text-danger-dark'] = true ; return obj
    },
    setRandomSeed () {
      this.seed = Date.now()
    },
    drawVeiculo () {
      const seededFn = seedrandom(this.seed)
      jStat.setRandom(seededFn)

      var ctx = this.$refs.myChart
      
      this.veiculoChart = this.veiculoChart || new Chart(ctx, {
        type: 'bubble',
        drawTicks: false,
        data: {
          datasets: [
            {
              label: 'Passageiros',
              data: undefined,
              //pointStyle: ['rectRot'],
              pointStyle: undefined,
              borderWidth: 1
            },
            {
              label: 'Distancia',
              data: undefined,
              borderWidth: 1
            }
          ]
        },
        options: {
          tooltips: {
            enabled: false
          },
          legend: {
            display: false
          },
          //responsive: false,
          maintainAspectRatio: false,
          //aspectRatio: 2,
          //responsiveAnimationDuration: 0,
        },
      })//veiculoChart

      
      // o raio da bolha não é plotado em pixels, portanto devemos calcular um fator
      // correspondente para converter de pixel para metro
      let chart = this.veiculoChart
      let xmarginRight = chart.scales['x-axis-0'].margins.right
      let xmarginLeft = chart.scales['x-axis-0'].margins.left

      //let ymarginRight = chart.scales['y-axis-0'].margins.right
      //let ymarginLeft = chart.scales['y-axis-0'].margins.left
      
      this.fatorEscala = (chart.chartArea.right-chart.chartArea.left)/this.input.comprimento

      chart.canvas.parentNode.style.height = (
        (this.input.largura * this.fatorEscala+xmarginRight+xmarginLeft)
      ).toFixed(2)+'px'
      
      
      // escala a imagem dos emojis para o tamanho médio das pessoas
      const tamanhoEmoji = this.pessoasDistViolacao.map(item => item.r).reduce((a, c) => a +c, 0)/this.pessoasDistViolacao.length
      this.emojis.forEach(item => {
        item.height = tamanhoEmoji//*1.1
        item.width = tamanhoEmoji//*1.1
      })


      chart.data.datasets[0].data = this.pessoasAtributos
      chart.data.datasets[0].pointStyle = this.pessoasAtributos.map(item => item.emoji)

      chart.data.datasets[1].data = this.pessoasDistViolacao
      chart.data.datasets[1].backgroundColor = this.riscoCor
      
      chart.options.scales = {
        yAxes: [{
          gridLines: {
            //drawBorder: false,
            drawTicks: false,
            display: false
          },
          ticks: {
            beginAtZero: true,
            max: parseFloat(this.input.largura),
            display: false
          }
        }],
        xAxes: [{
          gridLines: {
            //drawBorder: false,
            drawTicks: false,
            display: true
          },

          ticks: {
            beginAtZero: true,
            max: parseFloat(this.input.comprimento),
            display: false
          }
        }]
      }
      //this.nextTick(chart.update)
      chart.update()

      
    },//drawVeiculo
    drawCurvaCarga () {
      this.calculaPassageiros()
      // gráfico
      var ctx = this.$refs.cargaChart
      
      this.cargaChart = this.cargaChart || new Chart(ctx, {
        type: 'bar',
        //drawTicks: false,
        data: {
          datasets: [
            {
              label: 'carga',
              data: undefined,
              borderWidth: 0.1
            }
          ],
          labels: undefined,
        },
        options: {
          tooltips: {
            enabled: true
          },
          legend: {
            display: false
          },
          // responsive: true,
          maintainAspectRatio: false
        }
      })
      
      let chart = this.cargaChart

      chart.options.scales = {
        yAxes: [{
          gridLines: {
            drawTicks: false,
            display: false
          },
          ticks: {
            display: false
          }
        }],
        xAxes: [{
          barPercentage: 1.0,
          categoryPercentage: 1.0,
          gridLines: {
            drawTicks: true,
            display: false
          },
          ticks: {
            display: false
          }
        }]
      }
      
      const horarios = this.input.curvaDeCarga.map((item, i) => i)

      chart.data.labels = horarios.map(item => item + ' hs')
      chart.data.datasets[0].data = this.input.curvaDeCarga
      chart.data.datasets[0].borderWidth = 1

      
      if (this.cargaHorario.aplicarCarga) {
        chart.data.datasets[0].borderColor = horarios
          .map((item, i) => item === parseFloat(this.cargaHorario.horario) ? 'rgba(0, 117, 255, 1)' 
            : 'rgba(150, 35, 12, ' + (0.5 + parseFloat(this.input.curvaDeCarga[i])*0.6) + ')'
          )                
        // chart.data.datasets[0].borderColor = horarios
        //   .map((item, i) => item === parseFloat(this.cargaHorario.horario) ? 'rgba(0, 117, 255, 1)' 
        //     : 'rgba(150, 35, 12, ' + (0.5 + parseFloat(this.input.curvaDeCarga[i])*0.6) + ')'
        //   )
      } else {
        // chart.data.datasets[0].borderColor = new Array(horarios.length).fill('rgba(114, 120, 158, 1)')
        chart.data.datasets[0].borderColor = new Array(horarios.length).fill('blue')
      }

      chart.options.tooltips.enabled = this.cargaHorario.aplicarCarga
      chart.update()
    },
    drawCasosDr () {
      var ctx = this.$refs.casosChart
      this.casosChart = this.casosChart || new Chart(ctx, {
        type: 'line',
        plugins: ['annotation'],

        //drawTicks: false,
        data: {
          datasets: [
            {
              label: 'Média móvel (7 dias)',
              data: undefined,
              borderWidth: 0.4,
              borderColor: 'yellow',
              fill: 'origin',
              //backgroundColor: 'rgba(114, 120, 158, 0.2)'

            }
          ],
          labels: undefined,
        },
        options: {
          animation: {
            duration: 400
          },
          maintainAspectRatio: false,
          annotation: {
            annotations: [{
              drawTime: 'afterDatasetsDraw', // overrides annotation.drawTime if set
              id: 'a-line-1', // optional
              type: 'line',
              mode: 'vertical',
              scaleID: 'x-axis-0',
              value: undefined,
              borderColor: 'yellow',
              borderWidth: 2
            }]
          }
        }
      })
      
      let chart = this.casosChart

      chart.options.scales = {
        yAxes: [{
          // gridLines: {
          //   drawTicks: false,
          //   display: false
          // },
          // ticks: {
          //   display: false
          // }
        }],
        xAxes: [{
          type: 'time',
          time: {
              displayFormats: {
                  quarter: 'MMM YYYY'
              }
          },
          ticks : {
            callback: (label)  => {
              return translate_this_label( label )
            }
          }
        }]
      }
      
      chart.data.labels = this.casosDateLabels
      chart.data.datasets[0].data = this.dadosDrSel[1].map(item => Math.round(item*100)/100)

      chart.update()

    },
    drChange () {
      this.input.casosPorHabitante = Math.round(this.dadosDrSel[1][parseInt(this.dadosDrSelIndex)] * 100) / 100
    }

  },//methods
  mounted () {
    const native = false
    if (native) {
      '😷 😒 😬'.split(' ').map((emoji, i) => {
        let canvas = document.getElementById('emojiCanvas'+i)
        let ctx = canvas.getContext('2d')
        ctx.font = '80px serif'
        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle'
        ctx.fillText(emoji, canvas.width / 2, canvas.height / 2 + 5)
        var dataURL = canvas.toDataURL()
        var img = new Image()
        img.src = dataURL
        this.emojis.push(img)
      })
    } else {
      '😷 😒 😬'.split(' ').map((emoji, i) => {
        let img = document.getElementById('imgEmoji'+i)
        let wh = '20px'
        img.width = wh
        img.height = wh
        this.emojis.push(img)
      })
    }

    this.defineTrem()
    this.drawVeiculo()
    this.drawCurvaCarga()
    ;(function() {
        var throttle = function(type, name, obj) {
            obj = obj || window;
            var running = false;
            var func = function() {
                if (running) { return; }
                running = true;
                 requestAnimationFrame(function() {
                    obj.dispatchEvent(new CustomEvent(name));
                    running = false;
                });
            };
            obj.addEventListener(type, func);
        };
        throttle("resize", "optimizedResize");
    })();
    window.addEventListener("optimizedResize", this.drawVeiculo)
    //window.addEventListener('resize', this.$nextTick(this.drawVeiculo)) 

    this.$nextTick(() => {
      //this.drSel = this.drsNomes[0]
      this.drSel = 'Grande São Paulo'

      this.dadosDrSelIndex = this.dadosDrSel[0].length-1
      this.drawVeiculo()
    })

  },
  computed: {
    opcoesLugares () {
      return new Array(parseInt(this.qtdPassageirosCarga))
        .fill(0)
        .map((_, id) => Array(parseFloat(this.input.distanciamento)).fill(Object({id: id})).map(item => 
          Object({
            id: item.id,
            x: jStat.uniform.sample(
              0 + this.ppadd, this.input.comprimento - this.ppadd),
            y: jStat.uniform.sample(
              0 + this.ppadd, this.input.largura - this.ppadd),
            rp: jStat.normal.sample(this.pRaioMean, this.pRaioStd) * this.fatorEscala
            // x: 4,
            // y: 2,
            // rp: 1 * this.fatorEscala
          })
        )
      )//.sort((a, b) => Math.sqrt((a.x - b.x)*(a.x - b.x) + (a.y - b.y)*(a.y - b.y)))
    },//opcoesLugares
    pessoas () {
      let pessoasPosicionadas = []
      return this.opcoesLugares.map(item => {      

        let scores = item.map((x) => {
          return [x, (pessoasPosicionadas.map((pessoasPosicionada) => {
            let dx = Math.abs(pessoasPosicionada.x - x.x)
            let dy = Math.abs(pessoasPosicionada.y - x.y)
            let dist = Math.sqrt(dx*dx + dy*dy)
            return 1/dist// - 1/Math.abs(item[0].x - x.x)
          }).reduce((a, c) => c > a ? c : a , 0))]
        })
        
        let itemMelhorScore = scores.sort((a, b) => a[1]-b[1])[0][0]
        pessoasPosicionadas.push(itemMelhorScore)
        return itemMelhorScore
      })
    },
    pessoasDist () {
      return this.pessoas.map((item, i) => {
        const seededFn = seedrandom(i)
        jStat.setRandom(seededFn)
        return Object({
          id: item.id,
          x: item.x,
          y: item.y,
          rp: item.rp,
          r: ((item.rp/this.fatorEscala) + this.input.distanciaSegura) * this.fatorEscala,
          usaMascara: jStat.uniform.sample(0, 100) > (100 - parseFloat(this.input.adesaoMascara)),
          usaAstro: jStat.uniform.sample(0, 100) > (100 - parseFloat(this.input.adesaoAstronauta))
        })
      })
    },
    pessoasDistEmoji () {
      return this.pessoasDist.map((item, i) => {
        let res = Object({
          id: item.id,
          x: item.x,
          y: item.y,
          rp: item.rp,
          r: item.r,
          usaMascara: item.usaMascara,
          emoji: item.usaMascara ? this.emojis[0] : this.emojis[1+(i % 2)],
          emojiUTF: item.usaMascara ? '😷' : ['😒', '😬'][i % 2]
        })
        return res
      })
    },
    pessoasDistViolacao () {
      return this.pessoasDistEmoji.map(item => {
        let res = Object({
          id: item.id,
          x: item.x,
          y: item.y,
          rp: item.rp,
          r: item.r,
          usaMascara: item.usaMascara,
          emoji: item.emoji,
          emojiUTF: item.emojiUTF,
          violacoes: this.pessoasDistEmoji.reduce((a, c) => {
            if (c.id === item.id) return a
            let dx = Math.abs(item.x - c.x)
            let dy = Math.abs(item.y - c.y)
            let dist = Math.sqrt(dx*dx + dy*dy)
            if (dist < (item.r/this.fatorEscala + c.r/this.fatorEscala)) {
              a.push(c)
            }
            return a
          }, [])
        })
        res.violada = !!res.violacoes.length
        return res
      })
    },
    pessoasAtributos () {
      return this.pessoasDistViolacao.map(item => {
        return Object({
          id: item.id,
          x: item.x,
          y: item.y,
          r: item.rp,
          usaMascara: item.usaMascara,
          emoji: item.emoji,
          emojiUTF: item.emojiUTF,
          violacoes: item.violacoes,
          usaAstro: item.usaAstro
        })
      })
    },
    riscoContaminacaoPassageiros () {
      return this.pessoasAtributos.map((passageiro) => {
        let risco = 0
        let rhs
        if (passageiro.usaMascara) {
          risco = risco + passageiro.violacoes
            .reduce((a, c) => a + (c.usaMascara ? this.riscos.comCom : this.riscos.comSem), 0)
          
          rhs = passageiro.violacoes.map(x => x.usaMascara ? ''+this.riscos.comCom : ''+this.riscos.comSem)

        } else {
          risco = risco + passageiro.violacoes
            .reduce((a, c) => a + (c.usaMascara ? this.riscos.semCom : this.riscos.semSem), 0)
          
          rhs = passageiro.violacoes.map(x => x.usaMascara ? ''+this.riscos.semCom : ''+this.riscos.semSem)

        }
        
        let riscoRnd = Math.round(risco*100)/100

        let desc = passageiro.emojiUTF + (!passageiro.violacoes.length ? '' : '⇐' + passageiro.violacoes.map(item => item.emojiUTF).sort((a, b) => a-b).join(''))
        
        
        let res = Math.round((riscoRnd * this.probEstarContaminado)*10000)/10000

        let rhsClass = rhs.map(this.pClass)

        return [riscoRnd, desc, rhs, res, rhsClass]
      })
    },
    riscoMinMax () {
      let res = this.riscoContaminacaoPassageiros.sort((a, b) => a[3] - b[3])
      return [res[0], res[res.length-1]]
    },
    riscoContaminacaoPassageirosFiltro () {
      if (this.verTabelaCompleta) return this.riscoContaminacaoPassageiros
      return this.riscoMinMax
    },
    violacoesDeDistanciamento () {
      return this.pessoasDistViolacao.filter(item => item.violada).length
    },
    violacoesDeDistanciamentoTotal () {
      return this.pessoasDistViolacao.reduce((a, c) => a + c.violacoes.length, 0)
    },
    violacoesDeDistanciamentoMedia () {
      return Math.round(this.violacoesDeDistanciamentoTotal/this.qtdPassageirosCarga*10)/10
    },
    riscoBool () {
      const riscoThreshold = 2
      return this.riscoContaminacaoPassageiros
        .map(item => item[0] > riscoThreshold)
    },
    riscoCor () {
      return this.riscoBool
        .map(item => item ? 'rgba(255, 99, 132, 0.4)' : 'rgba(10, 200, 50, 0.4)')
    },
    // casosRiscoAlto () {
    //   return this.riscoBool.filter(item => item).length
    // },
    // casosRiscoAltoPct () {
    //   return (this.casosRiscoAlto/this.riscoBool.length*100).toFixed(0)+ ' %'
    // },
    // casosRiscoBaixo () {
    //   return this.riscoBool.length - this.casosRiscoAlto
    // },
    // casosRiscoBaixoPct () {
    //   return (this.casosRiscoBaixo/this.riscoBool.length*100).toFixed(0)+ ' %'
    // },
    cargaHorarioSel () {
      return this.input.curvaDeCarga[this.cargaHorario.horario]
    },
    qtdPassageirosCarga () {
      return this.input.qtdPassageiros

      // if (!this.cargaHorario.aplicarCarga) return this.input.qtdPassageiros
      // let cargaSel = this.input.curvaDeCarga[this.cargaHorario.horario]
      // return Math.max(1, Math.round(parseFloat(this.input.qtdPassageiros) * cargaSel))
    },
    probEstarContaminado () {
      return Math.round((this.input.casosPorHabitante * this.qtdPassageirosCarga / 100e3) * 100000)/100000
    },
    drsNomes () {
      return Object.keys(this.drs)
    },
    dadosDrSel () {
      if (!this.drSel) return [[],[]]
      return this.drs[this.drSel]
    },
    dadosDrCasosMax () {
      return this.dadosDrSel[1].reduce((a, c) => c > a ? c : a, 0)
    },
    casosDateLabels () {
      return this.dadosDrSel[0].map(item => {
        let s = item.toString()
        let d = new Date(s.slice(0,4)+'/'+s.slice(4,6)+'/'+s.slice(6,8))
        return d
      })
    },
    dataSel () {
      if (!this.casosDateLabels[this.dadosDrSelIndex]) return
      let d = this.casosDateLabels[this.dadosDrSelIndex]
      //console.log(d)
      return (d.getDay()+1)+'/'+(d.getMonth()+1)+'/'+d.getFullYear()
    }
  }
}

export default app

</script>
